






















import {initialProjectManagement} from "@/core/constants/api/initialProjectManagement";
import {paramsToQuery} from "@/core/utils/paramsToQuery";
import AsModal from "@/mixins/AsModal";
import {LabSerializerWithPopulate, ProjectManager} from "@/types/ApiTypes";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component
export default class AddProjectBySeqModal extends Mixins(AsModal) {
  @Getter("permission/id") readonly id!: number;
  @Prop(Number) readonly seq!: number;
  @Prop(String) readonly name!: string;

  @Emit("refresh") refresh(): void {}

  formData: ProjectManager = {...initialProjectManagement()};
  labList: LabSerializerWithPopulate[] = [];

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.formData.sequence = this.seq;
      this.api
        .getLabList({query: paramsToQuery({user: this.id}), fields: "id,name"})
        .then((res) => (this.labList = res));
    } else {
      this.clean();
    }
  }

  save() {
    this.api.addProjectManagement(this.formData).then(() => {
      this.refresh();
      this.clean();
    });
  }

  clean(): void {
    this.formData = {...initialProjectManagement()};
    this.dismiss();
  }
}
