











































import {initialTdb} from "@/core/constants/api/initialTdb";
import AsModal from "@/mixins/AsModal";
import {TransducerDB} from "@/types/ApiTypes";
import {Form} from "element-ui";
import {Component, Emit, Mixins, Prop, Watch} from "vue-property-decorator";

@Component
export default class EditTransducerModal extends Mixins(AsModal) {
  @Prop(Number) readonly id!: number;
  @Emit("refresh") refresh(): void {}

  @Watch("visible")
  onVisibleChange(val: boolean): void {
    if (val) {
      this.api.getTDBList({query: `id:${this.id}`}).then((res) => {
        this.formData = res[0];
      });
    }
  }

  formData: TransducerDB = {...initialTdb()};

  editTransducer(): void {
    (this.$refs.form as Form).validate((valid) => {
      if (valid) {
        this.api.editTransducerDB(this.formData, this.formData.id).then(() => {
          this.refresh();
          this.clean();
        });
      } else {
        return false;
      }
    });
  }

  clean(): void {
    this.formData = {...initialTdb()};
    this.dismiss();
  }

  rules = {
    device_category: [{required: true, message: "Device Category is required.", trigger: "change"}],
    name: [{required: true, message: "Name is required.", trigger: "change"}],
    category: [{required: true, message: "Category is required.", trigger: "change"}],
    serial_num: [{required: true, message: "Serial Number is required.", trigger: "change"}],
    sensitivity: [{required: true, message: "Sensitivity is required.", trigger: "change"}],
    sensitivity_unit_pa_per_v: [{required: true, message: "Sensitivity Unit is required.", trigger: "change"}],
  };
}
