





























































import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component
export default class AddPlaybackChannelModal extends Mixins(AsModal) {
  @Getter("permission/email") readonly email!: string;  
  @Emit("refresh") refresh(data): void {}

  @Prop({type: Array, default: () => []}) readonly audio;

  clean(): void {
    this.dismiss();
  }

  resetForm(): void {
    this.$nextTick(() => {
      const ref: any = this.$refs["playbackChannelRuleFormRef"];
      ref.resetFields();
    });
    this.playbackChannelRuleForm = {
      adjustment: -10,
      channelIndex: 1,
      name: "",
      sourceFileChannel: 1,
      sourceFileName: "",
      sourceFileLength: 15,
      delayTime: 3,
      skipTime: 5,
      takeLength: 10,
    };
  }

  addPlayback(): void {
    const ref: any = this.$refs["playbackChannelRuleFormRef"];
    ref.validate((valid) => {
      if (valid) {
        this.refresh(this.playbackChannelRuleForm);
        this.clean();
      } else {
        return false;
      }
    });
  }

  convertAudio(): void {
    this.api.doFormatConvertApi({
      "parameters": [
        {"name": "Target Channel", "value": this.playbackChannelRuleForm.sourceFileChannel},
        {"name": "Total Time", "value": this.playbackChannelRuleForm.sourceFileLength},
        {"name": "Delay Time", "value": this.playbackChannelRuleForm.delayTime},
        {"name": "Skip Time", "value": this.playbackChannelRuleForm.skipTime},
        {"name": "Take Time", "value": this.playbackChannelRuleForm.takeLength},
      ],
      "inputs": [
        [
          {"name": "Bucket Name", "value": "pal.data"},
          {"name": "Object Key", "value": `${this.email}/${this.playbackChannelRuleForm.sourceFileName}.wav`}
        ]
      ],
      "output": [
        {"name": "Bucket Name", "value": "pal.data"},
        {"name": "Object Key", "value": `${this.email}/${this.playbackChannelRuleForm.sourceFileName}.wav`}
      ]      
    }).then((res) => {
      if (res == "OK") {
        const name = this.playbackChannelRuleForm.sourceFileName + "_Converted";
        this.audio.push({name, url: name});
        this.playbackChannelRuleForm.sourceFileName = name;
      }
    })
  }

  playbackChannelRuleForm = {
    adjustment: 0,
    channelIndex: 1,
    name: "",
    sourceFileChannel: 1,
    sourceFileName: "",
    sourceFileLength: 15,
    delayTime: 3,
    skipTime: 5,
    takeLength: 10,
  };
  playbackChannelRule = {
    name: [{required: true, message: "Channel name is required.", trigger: "change"}],
  };
}
