







































import AsModal from "@/mixins/AsModal";
import {Component, Emit, Mixins, Prop} from "vue-property-decorator";
import {Getter} from "vuex-class";

@Component
export default class CalibrationEventModal extends Mixins(AsModal) {
  @Getter("permission/email") readonly email!: string;
  @Emit("refresh") refresh(): void {}
  @Emit("doCalibrate") doCalibrate(): void {}
  @Prop({type: String, default: () => ""}) readonly tabName!: string;
  @Prop({type: String, default: () => null}) readonly sn!: string;
  @Prop({type: Boolean, default: () => false}) readonly equalization!: boolean;
  excuteCallback: string = "";
  clean(): void {
    this.dismiss();
  }

  save(): void {
	let params = {};
	if (this.tabName !== "Input") {
      params = {
		InputChannel: 1,
		SensitivityFrequency: 1000,
		SourceLevel: 0.1,
		IsPhantomPowerOn: true
	  }
	} else if (!this.equalization) {
      params = {
		InputChannel: 1,
		OutputChannel: 1,
		SensitivityFrequency: 1000,
		SourceLevel: 0.5
	  }
	} else {
      params = {
		InputChannel: 1,
		OutputChannel: 1,
		SensitivityFrequency: 1000,
		SourceLevel: 0.5,
		EqStartFrequency: 100,
		EqStopFrequency: 10000
	  }
	}
	// Config Output
	const command = "calibrate;" + JSON.stringify(params);
	this.api.getPALSendCommandHandle({id: this.sn, cmd: command}).then((res) => {
	  let data = JSON.parse(res["cmd"]);
	  this.$emit("doCalibrate", data);
	  if ( this.equalization ) {
    	let file = data.RecordedFile.split("\temp")[1];
  	    this.api.s3upload({id: this.sn, src: file, dst: `s3://${this.email}/${file}.wav`}).then((res) => {
		  this.api.doExecuteApi({
			inputs: [
				[
					{"name": "Bucket Name", "value": "pal.data"},
					{"name": "Object Key", "value": `"${this.email}/${file}.wav`}
				]
			]			  
		  }).then((res) => {
			  this.excuteCallback = JSON.stringify(res);
  		  });
 	    });
	  }
	});	  
  }

  stop(): void {
	this.api.getPALSendCommandHandle({id: this.sn, cmd: "stop"}).then(() => {
	});	  
  }

  mockResultData = [{name: "Head Front"}, {name: "Head  Right"}, {name: "Head  Left"}];
  mockRequirementData = [{name: "Manual Point"}, {name: "Value Limit"}];
  chartOption = {
    tooltip: {
      trigger: "axis",
    },
    legend: {
      data: ["High", "Low"],
    },
    toolbox: {
      show: true,
      feature: {
        mark: {show: true},
        dataZoom: {show: true},
        dataView: {show: true},
        magicType: {show: true, type: ["line", "bar", "stack", "tiled"]},
        restore: {show: true},
        saveAsImage: {show: true},
      },
    },
    calculable: true,
    dataZoom: {
      show: true,
      realtime: true,
      start: 20,
      end: 80,
    },
    xAxis: [
      {
        type: "category",
        boundaryGap: false,
        data: (function() {
          var list = [];
          for (var i = 1; i <= 30; i++) {
            list.push("2013-03-" + i);
          }
          return list;
        })(),
      },
    ],
    yAxis: [
      {
        type: "value",
      },
    ],
    series: [
      {
        name: "High",
        type: "line",
        data: (function() {
          var list = [];
          for (var i = 1; i <= 30; i++) {
            list.push(Math.round(Math.random() * 30));
          }
          return list;
        })(),
      },
      {
        name: "Low",
        type: "line",
        data: (function() {
          var list = [];
          for (var i = 1; i <= 30; i++) {
            list.push(Math.round(Math.random() * 10));
          }
          return list;
        })(),
      },
    ],
  };
  ei: any;
}
